<template>
    <div>
        <login-logo/>
        <div class="signUp">
            <div class="signUp-head">
                <span></span>
            </div>
            <el-form v-model="registerForm" ref="registerForm" label-width="70px" class="demo-ruleForm" :rules="rules">
                <el-form-item prop="username" label="">
                    <el-input v-model="registerForm.username" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="password" label="">
                    <el-input type="password" v-model="registerForm.password" placeholder=""></el-input>
                </el-form-item>
                <div class="login-btn">
                    <el-button @click="goSignUp()"></el-button>
                    <el-button type="primary" @click="login()"></el-button>
                </div>
            </el-form>
        </div>
    </div>
</template>

<script>
    import LoginLogo from "./loginLogo";
    import {mixin} from "../../mixins";
    import {getImg, loginIn} from '../../networks/index'
    export default {
        name: "login-in",
        components: {
            LoginLogo,
        },
        mixins:[mixin],
        data(){
            return{
                registerForm:{
                    username:'',            //
                    password:'',            //
                },
                rules: {
                    username: [
                        {required:true,  trigger:'blur',  message:''  }
                    ],
                    password: [
                        {required:true,  trigger:'blur',  message:''  }
                    ]
                }
            }
        },
        activated(){
            //keep-alive
            this.$store.commit('setActiveName','');
        },
        mounted() {
            this.changeIndex('');
        },
        methods:{
              login(){
                  let baseURL = "http://localhost:3001/admin/api/user/getImg/"
                  loginIn(this.registerForm).then(res =>{
                  let _this = this;
                  console.log(res);
                  this.$message({
                      type:'success',
                      message:''
                  })

                  _this.$store.commit('setLoginIn',true)
                  _this.$store.commit('setUserId',res.data.id)
                  _this.$store.commit('setUsername',res.data.username)
                  const avator = baseURL+res.data.id
                  getImg(res.data.id).then(r =>{
                      console.log(r.status);
                      if(r.status !== 200){
                          _this.$store.commit('setAvator',require("../../assets/img/user.jpg"))
                      }else {
                          _this.$store.commit('setAvator',avator)
                      }
                  }).catch(err =>{
                      console.log(err);
                  })
                      // console.log(baseURL + res.data.id);
                      setTimeout(function () {
                      _this.$router.push('/');
                      _this.changeIndex('');
                  },1000)
                  })

                // let _this = this;
                // loginIn(this.registerForm).then(res =>{
                //     console.log(res);
                //     if(res.status == 200){
                //         // _this.notify('','success');
                //         this.$message({
                //             type:'success',
                //             message:''
                //         })
                //         setTimeout(function () {
                //             _this.$router.push({path:'/'});
                //         },1000)
                //     }
                // }).catch(err =>{
                //     this.$message({
                //         type:'success',
                //         message:''
                //     })
                // })
            },
            goSignUp(){
                  this.changeIndex('')
                this.$router.push({path:'/sign-up'});
            },
            //theheaderactive
            changeIndex(value){
                this.$store.commit('setActiveName',value);
            }
        },
    }
</script>

<style scoped lang="scss">
    @import "../../assets/css/sign-up";
</style>
