<template>
    <div class="play-bar" :class="{show:!toggle}">
        <div @click="toggle = !toggle" class="item-up" :class="{turn:toggle}">
            <i class=" iconfont icon-xiangshang-05" title="/" slot="reference" ></i>
        </div>
        <div class="kongjian">
            <!---->
            <div class="item" @click="prev">
                <i class=" iconfont icon-shangyishou_huaban" title="" slot="reference" ></i>
            </div>
            <!---->
            <div class="item" @click="togglePlay()">
                <i class="iconfont"  :class='playButtonUrl'  title="/" slot="reference" ></i>
            </div>
            <!---->
            <div class="item" @click="next">
                <i class=" iconfont icon-xiayishou_huaban" title="" slot="reference" ></i>
            </div>
            <!---->
            <div class="item-img" @click="toLyric">
                <img :src="picUrl" alt="" />
            </div>
            <!---->
            <div class="playing-speed">
                <!---->
                <div class="current-time">{{nowTime}}</div>
                <div class="progress-box">
                    <div class="item-song-title">
                        <div>{{this.title}}</div>
                        <div>{{this.artist}}</div>
                    </div>
                    <div ref="progress" class="progress" @mousemove="mousemove">
                        <!---->
                        <div class="bg" ref="bg" @click="updatemove">
                            <div class="curProgress" ref="curProgress" :style="{width:curLength+'%'}"></div>
                        </div>
                        <!---->
                        <div class="idot" ref="idot" :style="{left:curLength+'%'}" @mousedown="mousedown" @mouseup="mouseup">
                        </div>
                    </div>
                </div>
                <!---->
                <div class="left-time">{{songTime}}</div>
                <!---->
                <div class="item item-volume">
                    <i v-if="volume == 0" class=" iconfont icon-jingyin" title=""  slot="reference" ></i>
                    <i v-else class=" iconfont icon-yinliang" title="" slot="reference" ></i>
                    <el-slider class="volume" v-model="volume" :vertical="true" ></el-slider>
                </div>
                <!---->
                <div class="item"  @click="subSong">
                    <i class=" iconfont icon-shoucang " :class="[isActive ? 'active':'']" title="" slot="reference" ></i>
                </div>
                <!---->
                <div class="item" @click="download()">
                    <i class=" iconfont icon-xiazai" title="" slot="reference" ></i>
                </div>
                <!---->
                <div class="item" @click="changeAside()">
                    <i class=" iconfont icon-bofangliebiao" title="" slot="reference" ></i>
                </div>
                <!---->
                <div class="item" @click="changePlayMode()">
                    <i class="iconfont" :class="PlayMode()" title=""></i>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from "vuex";
    import {
        songLyric,
        songUrl,
        download,
        getServeLikedSong,
        deleteLikedSong,
        subServeLikedSong
    } from "../../networks";

    export default {
        name: "PlayBar",
        data(){
            return{
                nowTime:'00:00',     //
                songTime:'00:00',    //
                curLength:0,         //
                progressLength:0,    //
                mouseStartX:0,       //
                tag:false,           //
                volume:50,           //
                ly:'',               //
                toggle:true,         //
                playMode:{
                    loop:0,
                    suijixunhuan:1,
                    danquxunhuan1:2,
                },
                mode:0,
            }
        },
        computed:{
          ...mapGetters([
              'id',                     //id
              'url',                    //
              'isPlay',                 //
              'playButtonUrl',          //
              'picUrl',                 //
              'title',                  //
              'artist',                 //
              'duration',               //
              'curTime',                //
              'showAside',              //
              'listIndex',              //
              'listOfSongs',             //
              'autoNext',                //
              'isActive',                //
              'loginIn',                 //
              'userId',                  //id
              'AsidePlayList',            //
    ])
        },
        created() {
            this.PlayMode();
        },
        mounted() {
            //
            this.progressLength = this.$refs.progress.getBoundingClientRect().width;
            document.querySelector('.item-volume').addEventListener('click',function (e) {
                document.querySelector('.volume').classList.add('show-volume');
                e.stopPropagation();
            },false);
            document.querySelector('.volume').addEventListener('click',function (e) {
                e.stopPropagation();
            },false);
            document.addEventListener('click',function () {
                document.querySelector('.volume').classList.remove('show-volume');
            },false);
        },
        watch:{
            //
            isPlay(){
              if(this.isPlay){
                  this.$store.commit('setPlayButtonUrl','icon-zanting4');
              }else{
                  this.$store.commit('setPlayButtonUrl','icon-play');
              }
            },
            ///
            curTime(){
              this.nowTime = this.formatSeconds(this.curTime);
              this.songTime = this.formatSeconds(this.duration);
              this.curLength = (this.curTime / this.duration)*100;
           },
           //
           volume(){
             this.$store.commit('setVolume',this.volume/100);
           },
            //
            autoNext(){
                if(this.autoNext){
                    this.next();
                }
            },
            id(){
                this.$store.commit('setIsActive',false)
            },
            mode(){
                this.$store.commit("setMode",this.mode)
            }

        },
        methods:{
           //
           PlayMode(){
               return this.mode === this.playMode.loop? 'icon-loop': this.mode === this.playMode.suijixunhuan?'icon-suijixunhuan':'icon-danquxunhuan1'
           },
           changePlayMode(){
               if(this.mode>=2){
                   this.mode = 0;
                   this.PlayMode();
               }else {
                   this.mode = this.mode + 1
               }

           },
          //
          togglePlay(){
              if(this.isPlay){
                  this.$store.commit('setIsPlay',false);
              }else{
                  this.$store.commit('setIsPlay',true);
              }
          },
            //
           formatSeconds(value){
                let theTime = parseInt(value);
                let result = '';
                let hour = parseInt(theTime/3600);  //
                let minute = parseInt(theTime/60);  //
                let second = parseInt(theTime % 60); //
                if(hour>0){
                    if(hour<10){
                        result = '0' + hour + ":";
                    }else {
                        result = + hour + ":";
                    }
                }
                if(minute>0){
                    if(minute<10){
                        result += '0' + minute + ":";
                    }else {
                        result +=  minute + ":";
                    }
                }else {
                    result += "00:";
                }
                if(second>0){
                    if(second<10){
                        result += '0' + second;
                    }else {
                        result += second ;
                    }
                }else {
                    result += "00";
                }
                return  result;
            },

            //
            mousedown(e){
                this.mouseStartX = e.clientX;
                this.tag = true;
            },
            mouseup(){
                this.tag = false;
            },
            mousemove(e){
                if(!this.duration){
                    return false;
                }
                if(this.tag){
                    let movementX = e.clientX - this.mouseStartX ; //
                    let curLength = this.$refs.curProgress.getBoundingClientRect().width;
                    let newPercent = ((movementX+curLength)/this.progressLength)*100;
                    if(newPercent>100){
                        newPercent = 100;
                    }
                    this.curLength = newPercent;
                    this.mouseStartX = e.clientX;
                    this.changeTime(newPercent);

                }
            },
            //
            changeTime(percent){
              let newCurTime = (percent*0.01)*this.duration;
              this.$store.commit('setChangeTime',newCurTime)
            },
            //
            updatemove(e){
              if(!this.tag){
                  //
                  let curLength = this.$refs.bg.offsetLeft;
                  let  newPercent = ((e.clientX - curLength)/this.progressLength)*100;
                  if(newPercent>100){
                      newPercent = 100;
                  } else if(newPercent<0){
                      newPercent = 0;
                  }
                  this.curLength = newPercent;
                  this.changeTime(newPercent);
              }
            },
            //
            changeAside(){
              this.$store.commit('setShowAside',true);
            },
            //
            prev(){
              if(this.listIndex != -1 && this.AsidePlayList.length > 1){          //
                      if(this.listIndex >0){                                        //
                          this.$store.commit('setListIndex',this.listIndex-1);  //
                      }else{                                                                           //
                          this.$store.commit('setListIndex',this.AsidePlayList.length-1);  //
                      }
                      this.toPlay(this.AsidePlayList[this.listIndex].id);
              }
            },
            //
            next(){
                if(this.listIndex != -1 && this.AsidePlayList.length > 1){          //
                    if(this.mode === 0){
                        if(this.listIndex < this.AsidePlayList.length-1){               //
                            this.$store.commit('setListIndex',this.listIndex+1);  //
                        }else{                                                                           //
                            this.$store.commit('setListIndex',0);  //
                        }
                    }else if(this.mode === 1){
                        const long = this.AsidePlayList.length;
                        if(this.listIndex < this.AsidePlayList.length-1){               //
                            this.$store.commit('setListIndex',this.listIndex+Math.round(Math.random()*long));  //
                            this.$store.commit("setIsPlay",true)
                        }else{                                                                           //
                            this.$store.commit('setListIndex',Math.round(Math.random()*long));  //0
                        }
                    }else{
                        this.$store.commit('setListIndex',this.listIndex+0);  //
                        this.$store.commit("setIsPlay",true)
                    }
                    this.toPlay(this.AsidePlayList[this.listIndex].id);
                }
            },

            //
            toPlay(id){
              if(id&&id !=this.id){
                  this.$store.commit('setId',this.AsidePlayList[this.listIndex].id);
                  this.getUrl(this.AsidePlayList[this.listIndex].id);
                  this.$store.commit('setPicUrl',this.AsidePlayList[this.listIndex].al.picUrl);
                  this.$store.commit('setTitle',this.AsidePlayList[this.listIndex].name);
                  this.$store.commit('setArtist',this.AsidePlayList[this.listIndex].ar[0].name);
                  this.getLyric(this.AsidePlayList[this.listIndex].id);
                  if(this.loginIn){
                      getServeLikedSong(this.userId).then(res =>{
                          for(let item of res.data){
                              if(item.tid == id){
                                  this.$store.commit('setIsActive',true);
                                  break;
                              }
                          }
                      })
                  }
              }
            },
            //url
            getUrl(id){
                songUrl(id).then(res =>{
                    this.$store.commit('setUrl',res.data[0].url);
                    console.log(res.data[0].url);
                }).catch(err => {
                    console.log(err);
                });
            },
            //
            getLyric(id){
                songLyric(id).then(res =>{
                    // console.log(res.lrc.lyric);
                    this.$store.commit('setLyric',res.lrc.lyric);
                }).catch(err => {
                    console.log(err);
                });
            },

            // //
            // parseLyric(text){
            //     let lines = text.split("\n");                   //
            //     let pattern = /\[\d{2}:\d{2}.(\d{3}|\d{2})\]/g;   //
            //     let result = [];
            //     //
            //     if(!(/\[.+\]/.test(text))){
            //         return [[0,text]];
            //     }
            //     //
            //     while (!pattern.test(lines[0])){
            //         lines = lines.slice(1);
            //     }
            //     //
            //     for(let item of lines){
            //         let time = item.match(pattern);              // 
            //         let value = item.replace(pattern,'');        //
            //         for(let item1 of time){
            //             let t = item1.slice(1,-1).split(':');    //
            //             if(value!=''){
            //                 result.push([parseInt(t[0],10)*60 + parseFloat(t[1]),value]);
            //             }
            //         }
            //     }
            //     //----
            //     result.sort(function (a,b) {
            //         return  a[0]-b[0];
            //     });
            //     return result;
            // },
            //
            toLyric(){
              this.$router.push({path:'/lyric'});
            },
            //
            download(){
              download(this.url).then(res =>{
                  console.log(res);
                  let content = res.data;
                  console.log(this.artist);
                  console.log(this.title);
                  let eleLink = document.createElement('a');
                  eleLink.download = `${this.artist}-${this.title}.mp3`;
                  eleLink.style.display = 'none';
                  //blob
                  let blob = new Blob([content]);
                  eleLink.href = URL.createObjectURL(blob);
                  //document
                  document.body.appendChild(eleLink);
                  //
                  eleLink.click();
                  //
                  document.body.removeChild(eleLink);
              }).catch(err =>{
                  console.log(err);
              })
            },
            //
            subSong() {
                if (!this.id) {
                    this.$message.error('');
                } else {
                    if (!this.isActive) {
                        if (this.loginIn) {
                            const params = {
                                user_id: this.userId,
                                tid: this.id,
                                time: (new Date()).getTime(),
                            }
                            subServeLikedSong(params).then(res => {
                                if (res.status == 200) {
                                    this.$store.commit("setIsActive", true)
                                    this.$message.success('');
                                } else {
                                    this.$message.success('');
                                }
                            })
                        } else {
                            this.$message.error('')
                        }
                    } else {
                        deleteLikedSong(this.id).then(res =>{
                            if(res.status == 200){
                                this.$message.success('')
                                this.$store.commit('setIsActive',false)
                            }else {
                                this.$message.error('')
                            }
                        })
                    }
                }
            }
        },
    }
</script>

<style scoped lang="scss">
@import "../../assets/css/play-bar";

</style>
