<template>
    <div class='video'>
        <video
            ref="videoPlayer"
            class="video-js video-skin"
        ></video>
    </div>
</template>

<script>
import videojs from 'video.js'
import '../../plugins/video-quality.js'
import 'video.js/dist/video-js.css'
import '../../assets/css/video.scss'
window.videojs = videojs
require('video.js/dist/lang/zh-CN')
export default {
    name: 'videoPlayer',
    components: {},
    props: {
        options: {
            type: Object,
            default () {
                return {}
            }
        }
    },
    data () {
        // 
        return {
            opt: {
                playbackRates: [0.5, 1.0, 1.5, 2.0], // 
                controls: true,
                autoplay: false, // true,
                muted: false, // 
                loop: false, // 
                preload: 'auto', // auto,
                // language: 'zh-cn', // 
                // languages: {
                //     'zh-CN': {
                //         Play: '',
                //         Pause: '',
                //         Mute: '',
                //         Unmute: '',
                //         'Playback Rate': ''
                //     }
                // },
                aspectRatio: '16:9', // "16:9""4:3"
                fluid: true, // true
                // sources: [{
                //     type: '',
                //     src: '' // url
                // }],
                // poster: '', // 
                notSupportedMessage: '', // 
                controlBar: {
                    currentTimeDisplay: true, // 
                    timeDivider: true, // 
                    durationDisplay: true, // 
                    remainingTimeDisplay: false, // 
                    volumePanel: { // 
                        inline: false // 
                    },
                    QualitySelector: true,
                    children: [
                        'playToggle', // 
                        'volumePanel', // 
                        'progressControl', // 
                        'currentTimeDisplay', // 
                        'timeDivider', //  / 
                        'durationDisplay', // 
                        'PlaybackRateMenuButton', // 
                        'qualityButton', // 
                        'fullscreenToggle' // 
                    ]
                },
                qualityList: [], // 
                quality: {}, // 
                qualityType: { // 
                    240: '',
                    360: '',
                    480: '',
                    720: '',
                    1080: '1080P'
                },
                errorDisplay: false,
                posterImage: false,
                bigPlayButton: true,
                textTrackDisplay: false
            },
            player: null
        }
    },
    mounted () {
        const self = this

        this.$nextTick(() => {
            const d = { ...this.opt, ...this.options }
            this.player = videojs(this.$refs.videoPlayer, d, function onPlayerReady () {
                self.$emit('ready', this, '')

                this.on('loadstart', () => {
                    self.$emit('loadstart', this, '')
                })
                this.on('progress', () => {
                    self.$emit('progress', this, '')
                })
                this.on('loadedmetadata', () => {
                    self.$emit('loadedmetadata', this, '')
                })
                this.on('canplaythrough', () => {
                    self.$emit('canplaythrough', this, '')
                })
                this.on('waiting', () => {
                    self.$emit('waiting', this, '')
                })
                this.on('play', () => {
                    self.$emit('play', this, '')
                })
                this.on('playing', () => {
                    self.$emit('playing', this, '')
                })
                this.on('pause', () => {
                    self.$emit('pause', this, '')
                })
                this.on('ended', () => {
                    self.$emit('ended', this, '')
                })
                this.on('error', () => {
                    self.$emit('error', this, 'error')
                })
                this.on('seek', () => {
                    self.$emit('seek', this, '')
                })
                this.on('seeking', () => {
                    self.$emit('seeking', this, '')
                })
                this.on('seeked', () => {
                    self.$emit('seeked', this, '')
                })
                this.on('ratechange', () => {
                    self.$emit('ratechange', this, '')
                })
                this.on('timeupdate', () => {
                    self.$emit('timeupdate', this, '')
                })
                this.on('volumechange', () => {
                    self.$emit('volumechange', this, '')
                })
                this.on('stalled', () => {
                    self.$emit('stalled', this, '')
                })
                this.on('qualityChange', () => {
                    self.$emit('qualityChange', this, '')
                })
            })
        })
    },
    beforeDestroy () {
        if (this.player) {
            this.player.dispose()
        }
    },
    //  data
    computed: {
        src () {
            return this.options.sources[0].src
        }
    },
    // 
    methods: {
    },
    watch: {
        src () {
            this.player.src(this.src)
            // MV 
            this.player.play()
        }
    }
}
</script>
<style scoped lang="scss">
.video{
    margin-top: 70px;
}
::v-deep.vjs-has-started.vjs-user-inactive.vjs-playing .vjs-control-bar {
    visibility: visible;
    opacity: 1;
}
</style>
