<template>
    <div>
        <login-logo/>
        <div class="signUp">
            <div class="signUp-head">
                <span></span>
            </div>
            <el-form v-model="registerForm" ref="registerForm" label-width="70px" class="demo-ruleForm" :rules="rules">
                <el-form-item prop="username" label="">
                    <el-input v-model="registerForm.username" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="password" label="">
                    <el-input type="password" v-model="registerForm.password" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="sex" label="">
                    <el-radio-group v-model="registerForm.sex">
                        <el-radio :label="0"></el-radio>
                        <el-radio :label="1"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item prop="phoneNum" label="">
                    <el-input  v-model="registerForm.phoneNum" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="email" label="">
                    <el-input  v-model="registerForm.email" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="birth" label="">
                    <el-date-picker v-model="registerForm.birth" placeholder="" style="width:100%;"></el-date-picker>
                </el-form-item>
                <el-form-item prop="introduction" label="">
                    <el-input  v-model="registerForm.introduction" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="location" label="">
                    <el-select  v-model="registerForm.location" placeholder="" style="width:100%;">
                        <el-option v-for="item in cities" :key="item.value" :label="item.label" :value="item.value"> </el-option>
                    </el-select>
                </el-form-item>
                <div class="login-btn">
                    <el-button @click="goback(-1)"></el-button>
                    <el-button type="primary" @click="SignUp()"></el-button>
                </div>
            </el-form>
        </div>
    </div>
</template>

<script>
    import {cities,rules}  from "../../assets/data/form"
    import LoginLogo from "./loginLogo";
    import {mixin} from "../../mixins";
    import {postSignUp} from  '../../networks/index'
    export default {
        name: "sign-in",
        components: {
            LoginLogo,
        },
        mixins:[mixin],
        data(){
            return{
                registerForm:{
                    username:'',            //
                    password:'',            //
                    sex:'',                 //
                    phoneNum:'',            //
                    email:'',               //
                    birth:'',               //
                    introduction:'',        //
                    location:'',            //
                    avator:'../../assets/img/user.jpg',              //
                },
                cities:[],                  //--
                rules:{},                   //
            }
        },
        created() {
            this.cities = cities;
            this.rules =  rules;
        },
        activated(){
            //keep-alive
            this.$store.commit('setActiveName','');
        },
        methods:{
            SignUp(){
                let _this = this;
                postSignUp(this.registerForm).then(res =>{
                    if(res.status == 200){
                            // _this.notify('','success');
                        this.$message({
                            type:'success',
                            message:''
                         })
                        setTimeout(function () {
                            _this.$router.push({path:'/login-in'});
                        },1000)
                    }
                }).catch(err =>{
                    console.log(err);
                })

            },
            goback(index){
                this.$router.go(index);
            },
        },
    }
</script>

<style scoped lang="scss">
    @import "../../assets/css/sign-up";
</style>
